
import { User } from "./user";

export class Log {
    eventtype = 'game';
    duration = 0;
    score = 0;
    '@timestamp': string;
    interrupted = false;
    difficulty: number;
    player_id: number;
    region: number;
    education: string;
    sex: number;
    age: number;
    pause_count = 0;
    pause_time = 0;
    
    game_specific_data: any;
    
    host_domain = "multilogic.hu";



    constructor (
        private user: User,
        public game_name: string,     
        public start_time: string, 
        public ui_device?: string,
        public game_version? : string,
        public ui_agent? : any,
        public ui_width? : number,
        public ui_height?: number
        ) { 
            this.initLog()
            
        }

    interrupt(){
        this.interrupted = true;
    }

    send(){
        let today = new Date()
        this["@timestamp"] = today.toISOString();
        console.log(this)

    }

    initLog(){
        this.player_id = this.user.player_id;
        
        this.region = this.user.region;

        this.age = new Date().getFullYear() - this.user.birthYear
        
        this.sex = this.user.sex;

        this.education = this.user.education;
       

    }

    
}