import * as PIXI from "pixi.js";
import {colors, /*game, height, initParams, width*/} from "./planartest-init";
import {Map} from "./map";
import {Delay, FadeTo, scheduler, Sequence} from "anim";
import { Level, PlanartestLog } from "../../models/game-models/planartest-log";
import { ComponentGamePlanar } from "./component-game-planar";

export class MapManager extends PIXI.Container {
	private currLevel: number = 0;
	private map: Map;
	private levelAnim: Sequence;
	private levelText: PIXI.Text;
	private levelStartTime
	private endOfLevelAnim: Delay;
	public maxLevel = 3;

	public constructor(private dim: any, private specialLog: PlanartestLog, private game: ComponentGamePlanar) {
		super();
		this.currLevel = 0;

		const background = new PIXI.Graphics();
		background.beginFill(colors.DARK);
		background.drawRect(0, 0, this.dim.w, this.dim.h);
		background.endFill();
		this.addChild(background);

		this.endOfLevelAnim = new Delay().setDuration(800);
		this.endOfLevelAnim;
		this.levelAnim = new Sequence([
			new Delay().setDuration(500),
			new FadeTo(1).setDuration(200),
			new Delay().setDuration(700),
			new FadeTo(0).setDuration(200),
			new Delay().setDuration(300)
		]);
		this.levelText = new PIXI.Text("", {align: 'center', fontSize: Math.min(70, 70 * this.dim.h / 815), fill: 0xaaaaaa});
		this.levelText.position.set(this.dim.w / 2, this.dim.h / 2);
		this.levelText.anchor.set(0.5, 0.5);
		this.levelText.alpha = 0;

		this.startMap();
	}

	private async startMap(): Promise<void> {
		if (this.map) {
			this.map.dispose();
			this.removeChild(this.map);
		}
		await this.startLevelAnim();
		this.levelStartTime = Date.now();
		this.map = new Map(this.settings, this.dim, this.mapFinished);
		this.addChild(this.map);
	}

	private async startLevelAnim(): Promise<PIXI.Container> {
		switch (this.currLevel) {
			case 0: this.levelText.text = "first round"; break;
			case 1: this.levelText.text = "second round"; break;
			default: this.levelText.text = "last round";
		}
		this.addChild(this.levelText);
		return scheduler.run(this.levelAnim, this.levelText);
	}

	private get settings(): ISettings {
		const result: ISettings = {
			maxDeg: 0,
			vertexNumber: 0,
			isHard: false,
		};
		switch (this.currLevel) {
			case 0:
				result.maxDeg = 5;
				result.vertexNumber = 9;
				break;
			case 1:
				result.maxDeg = 6;
				result.vertexNumber = 11;
				break;
			default:
				result.maxDeg = 7;
				result.vertexNumber = 13;
		}
		return result;
	}

	public readonly mapFinished = (): void => {
		console.log(this.map.numberOfClicks)
		let duration = Date.now()-this.levelStartTime
		console.log(duration)
		this.specialLog.addNewLevel(new Level(this.map.numberOfClicks, duration))
		//const stat = initParams.api.gameLog.stat;
		//stat[this.currLevel].nofSteps = this.map.numberOfClicks;
		//stat[this.currLevel].playTime = initParams.api.timer.duration - this.levelStartDuration;
		this.currLevel++;
		if (this.currLevel >= this.maxLevel) {
			this.game.calculateScore();
			console.log(this.specialLog)
		}
		else {
			this.startMap();
		}
	};
}

export interface ISettings {
	maxDeg: number;
	vertexNumber: number;
	isHard: boolean;
}